import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.css'
import LoadFile from './LoadFile.js';
import PageBar from './PageBar.js';
import Record from './Record.js';
import FilterItem from './FilterItem.js';
import ChartSummary from './ChartSummary.js';
import SearchItem from './SearchItem.js';
import FilterDistinct from './FilterDistinct.js';
import FilterTime from './FilterTime.js';

function App() {
  const [getSeqId, setSeqId] = React.useState(1);
  const [getIndex, setIndex] = React.useState();
  const [getSearch, setSearch] = React.useState();
  const [getResults, setResults] = React.useState();
  const [getPage, setPage] = React.useState();
  const [getPageSize, setPageSize] = React.useState(20);
  const [getSearchHistory, setSearchHistory] = React.useState([])
  const [getFavorite, setFavorite] = React.useState({})

  const [getFilter, setFilter] = React.useState()

  const runSearch = (term) => {
    console.log('[Search]', 'term:', term)
    try {
      let matches = getIndex.index.search(term);

      if (matches.length === 0) {
        throw { message: "Not Found" };
      }

      let recs = []
      matches.forEach((item) => {
        recs.push(parseInt(item.ref))
      })

      let results = {
        id: performance.now(),
        term: term,
        records: recs,
      }

      setSearchHistory([...getSearchHistory, results]);

      setPage(0)
      setResults(results)
    }
    catch (e) {
      console.error('ERROR!', e.message)
      let results = {
        error: e.message,
        id: performance.now(),
        term: '',
        records: [],
      }
      setPage(0)
      setResults(results)
    }
  }

  const ShowSearch = (id) => {
    for (let n = 0; n < getSearchHistory.length; n++) {
      let results = getSearchHistory[n]
      if (results.id === id) {
        setFilter()
        setPage(0)
        setResults(results)
        return;
      }
    }
  }

  const RemoveSearch = (id) => {

    let searches = [];
    let beforefound;
    let last;
    getSearchHistory.forEach((term) => {
      if (term.id === id) {
        beforefound = last;
      }
      else {
        searches.push(term)
      }
      last = term;
    });

    setSearchHistory(searches);

    setFilter()
    setPage(0);
    setResults(beforefound)
  }

  const ToggleFavorite = (e) => {
    //console.log('ToggleFavorite', e)
    let fav = {
      ...getFavorite
    }

    fav[e] = fav[e] ? false : true;

    let rec = [];
    Object.keys(fav).forEach((item) => {
      if (fav[item]) {
        rec.push(item)
      }
    })

    let hist = [
      ...getSearchHistory
    ]

    hist.forEach((item) => {
      if (item.id === -1) {
        item.records = rec
      }
    })

    setSearchHistory(hist)
    setFavorite(fav)
  }

  const FilterActivity = (type, id) => {
    if (type === 'ActivityId') {
      let actid = getIndex.records[id].ActivityId;
      let recs = []
      getIndex.records.forEach((rec) => {
        if (rec.ActivityId === actid) {
          recs.push(rec.id)
        }
      })
      if (recs.length > 0) {
        let results = {
          id: performance.now(),
          term: "Activity:" + actid,
          records: recs
        }
        FilterResult(results)
      }
    }
  }

  const UpdateIndex = (idx) => {
    let recs = idx.records.map((item) => {
      return item.id;
    });

    let results = {
      id: 0,
      term: "ALL",
      path: "all",
      records: recs,
    }

    setSearchHistory([
      { id: -1, term: "Favorites", path: "fav", records: [] },
      results,
    ]);

    setIndex(idx)
    setFilter()
    setPage(0)
    setResults(results)
  }

  const FilterResult = (results) => {
    results.id = getSeqId
    results.path = getResults.path + "#" + results.id.toString()
    setSeqId(results.id+1)
    setSearchHistory([...getSearchHistory, results]);
    setFilter()
    setPage(0)
    setResults(results)
  }

  const ToggleFilter = (name) => {
    if (getFilter === name) {
      setFilter(null)
    }
    else {
      setFilter(name)
    }
  }

  let index = getIndex
  let results = getResults

  // ****************************************

  let searches = [];

  let curId = getResults ? getResults.id : -1;

  getSearchHistory.map((item) => {
    let title = item.id === 0 ? "ALL" : item.id === -1 ? "Fav" : "#" + item.id + "-" + item.records.length
    let path = results.path.split("#")

    let isPath = false
    let className = "btn btn-light mx-1"
    path.forEach(it => {
      if (it === "all" && item.id === 0)
      {
        isPath = true
      }

      if (it === "fav" && item.id === -1)
      {
        isPath = true
      }

      if (item.id.toString() === it)
      {
        isPath = true
      }
    })
    if (curId === item.id) {
      className = "btn btn-primary mx-1"
    }

    searches.push(
      <button className={className}
        key={searches.length} title={item.term}
        onClick={() => { ShowSearch(item.id); }}
      >{isPath ? <b>{title}</b> : <span>{title}</span>}</button>
    )
    return null;
  })

  let items = [];
  if (index) {
    if (index.index) {
      items.push(
        <div>
          <input className="me-1" type='text' onChange={(e) => { setSearch(e.target.value); e.stopPropagation(); }}></input>
          <button onClick={(e) => { runSearch(getSearch); e.stopPropagation(); }}>search</button>
        </div>
      )

      if (results && results.error) {
        items.push(
          <div>Error: {results.error}</div>
        )
      }

      items.push(<hr />)
    }

    // ****************************************

    items.push(
      <div>
        {searches}
      </div>
    )

    if (results) {
      items.push(
        <div>
          <span className="mx-1">#{results.path}</span>
          <span className="badge text-bg-info mx-1">{results.term}</span>
          <span className="me-4">{results.records.length} records.</span>
          {results.id > 0 &&
            <span>
              [&nbsp;
              <a href="#" className="link-underline-primary" onClick={() => { RemoveSearch(results.id) }}>remove</a>
              &nbsp;]
            </span>
          }
        </div>
      )
    }

    // ****************************************

    if (results && results.records.length > 0) {
      items.push(<hr />)

      let curFilter = getFilter
      let terms = ["Search", "Date", "Time", "Level", "Stream", "ErrorType", "Pid", "Tid", "File", "Function", "Chart", "Distinct"]

      terms.forEach((item) => {
        items.push(
          <button className={curFilter === item ? "btn btn-primary mx-1" : "btn btn-light mx-1"}
            onClick={() => { ToggleFilter(item) }}>
            {item}
          </button>
        )
      })

      // ****

      if (curFilter)
      {
        switch (curFilter) {
          case "Time":
            items.push(
              <div><FilterTime Index={getIndex} Results={getResults.records} FilterResult={FilterResult} /></div>
            )
            break;
  
          case "Chart":
            items.push(
              <div><ChartSummary Index={getIndex} Results={getResults.records} /></div>
            )
            break;
  
          case "Search":
            items.push(
              <div>
                <SearchItem Index={getIndex} Results={getResults} FilterResult={FilterResult} />
              </div>
            )
            break;
  
          case "Distinct":
            items.push(
              <div>
                <FilterDistinct Index={getIndex} Results={getResults} FilterResult={FilterResult} />
              </div>
            )
            break;
  
          default:
            items.push(
              <div>
                <FilterItem Index={getIndex} Results={getResults.records} Item={curFilter} FilterResult={FilterResult} />
              </div>
            )
            break;
        }
      }

      // ****************************************

      items.push(<hr />)

      items.push(
        <div className="mb-2" >
          <PageBar Results={getResults} Favorite={getFavorite} Page={getPage} PageSize={getPageSize} OnPage={(e) => { setPage(e); }}>test</PageBar>
          <span className="me-4">&nbsp;</span>
          [&nbsp;
          <a href="#" className="link-underline-primary me-1"
            onClick={(e) => { setPageSize(10); setPage(0); }}
          >{getPageSize === 10 ? <b>10</b> : "10"}</a>
          <a href="#" className="link-underline-primary me-1"
            onClick={(e) => { setPageSize(20); setPage(0); }}
          >{getPageSize === 20 ? <b>20</b> : "20"}</a>
          <a href="#" className="link-underline-primary me-1"
            onClick={(e) => { setPageSize(50); setPage(0); }}
          >{getPageSize === 50 ? <b>50</b> : "50"}</a>
          <a href="#" className="link-underline-primary me-1"
            onClick={(e) => { setPageSize(100); setPage(0); }}
          >{getPageSize === 100 ? <b>100</b> : "100"}</a>
          ]
        </div>
      )

      let recs = [];
      for (let n = getPage; n < Math.min(getPage + getPageSize, results.records.length); n++) {
        let rec = index.records[results.records[n]];

        recs.push(
          <Record key={recs.length} Record={rec} Favorite={getFavorite} onFavorite={ToggleFavorite} onFilterActivity={FilterActivity} />
        )
      }

      items.push(
        <div className="container">
          {recs}
        </div>)
    }
  }
  else {
    items.push(
      <header className="App-header">
        <LoadFile OnUpdateIndex={UpdateIndex} />
      </header>
    )
  }

  return (
    <div className="App">
      {items}
    </div>
  );
}

export default App;
