import React, { useEffect } from 'react';
import Papa from 'papaparse';
import './App.css';

function LoadFile(props) {
  const [getStatus, setStatus] = React.useState();
  const [getContext, setContext] = React.useState();

  const RunLoadFile1 = () => {
    let context = getContext
    if (!context || !context.mode) {
      return
    }

    if (context.mode === "Start") {
      let context = {
        ...getContext,
        mode: "PapaStep",
        recs: [],
        lines: 0,
      }
      setContext(context)
      setStatus("Running Papa...")

      let errors;

      Papa.parse(context.file, {
        header: true,
        quoteChar: '"',
        worker: true,
        step: (row) => {
          let newrow = row.data;
          if (row.errors) {
            errors = row.errors;
          }

          if (!newrow['[0] Time Stamp']) {
            return;
          }

          // Normalize each records

          let dtstr = newrow['[0] Time Stamp'];
          delete newrow['[0] Time Stamp']
          delete newrow.SchemaID
          let dt = dtstr.split("T");
          newrow.Date = dt[0];
          newrow.Time = dt[1].replace("Z", "");
          newrow.id = context.lines;
          context.recs.push(newrow);
          context.lines++;

          if (context.lines > 0 && 0 === context.lines % 10000)
          {
            setStatus("PapaStep:" + context.lines)
            setContext({
              ...context
            })
          }
        },
        complete: () => {
          if (errors)
          {
            console.error("[Parse Error]", errors)
          }

          console.log("Finished:", context.recs.length);

          setStatus("PapaDone")
          setContext({
            ...context,
            mode: "PapaDone",
          })
        }
      });
    }

    if (context.mode === "PapaStep") {
      setStatus("PapaStep:" + context.recs.length)
    }

    if (context.mode === "PapaDone") {
      let snap = {
        filename: context.filename,
        records: context.recs,
        index: null,
      }

      setContext()
      setStatus("Papa Done");

      if (props.OnUpdateIndex) {
        console.log('[SNAP]', snap)
        props.OnUpdateIndex(snap)
      }
    }
  }

  const filechange = (e) => {
    let file = e.target.files[0];
    let context = {
      mode: 'Start',
      filename: file.name,
      file: file
    }
    setContext(context);
    setStatus(context.mode)
    console.log("filechange", context)
  }

  useEffect(() => {
    //console.log("UseEffect", getContext)
    if (getContext) {
        RunLoadFile1();
    }
  }, [getContext])

  console.log("VL:", getContext)

  let items = [];
  items.push(
    <span key={items.length}>
      { !getContext &&
      <div>
          <input id='myfile' style={{display: "none"}}type='file' accept='.csv' onChange={filechange} />
          <label className="btn btn-primary" htmlFor="myfile">Load MAEventTable.csv</label>
        </div>
      }
      <div>{getStatus}</div>
    </span>
  );

  return (
    <div className="App">
      {items}
    </div>
  );
}

export default LoadFile;
