import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.css'

function FilterTime(props) {
  const [getIndex, setIndex] = React.useState();
  const [getItem, setItem] = React.useState();
  const [getSelection, setSelection] = React.useState({});

  if (props.Item !== getItem) {
    setIndex()
    setSelection({})
    setItem(props.Item)
  }

  const SelectClearItem = () => {
    setSelection({})
  }

  const SelectInvertItem = () => {
    let sel = {}
    Object.keys(getIndex).forEach((item) => {
      if (!getSelection[item]) {
        sel[item] = true
      }
    })
    setSelection(sel)
  }

  const SelectItem = (name) => {
    let sel = {
      ...getSelection
    }

    let v = sel[name]
    if (!v) {
      v = false;
    }
    sel[name] = v ? false : true

    setSelection(sel)
  }

  const SelectFilterItem = () => {
    let recs = []

    let hasHH = false
    let hasMM = false
    let hhNames = []
    let mmNames = []

    Object.keys(getSelection).forEach(sel => {
      if (getSelection[sel]) {
        let ty = sel.substring(0, 3)
        if (ty === "hh:") {
          hasHH = true
          hhNames.push(sel.substring(4))
        }
        else if (ty === "mm:") {
          hasMM = true
          mmNames.push(sel.substring(4))
        }
      }
    })

    props.Results.forEach((item) => {
      let rec = props.Index.records[item]
      let hh = "hh: " + rec.Time.substring(0, 2)
      let mm = "mm: " + rec.Time.substring(3, 5)

      if (hasHH && hasMM) {
        if (getSelection[hh]) {
          if (getSelection[mm]) {
            recs.push(rec.id)
          }
        }
      }
      else if (hasHH) {
        if (getSelection[hh]) {
          recs.push(rec.id)
        }
      }
      else if (hasMM) {
        if (getSelection[mm]) {
          recs.push(rec.id)
        }
      }
    })

    if (recs.length === 0)
    {
      return
    }

    console.log("[FilterTime]", "found:", recs.length)

    hhNames.sort();
    mmNames.sort();
    let hhNamesTxt = hhNames.join(",")
    let mmNamesTxt = mmNames.join(",")
    let name = "hh:[" + hhNamesTxt + "], mm:[" + mmNamesTxt + "]"

    if (props.FilterResult) {
      let results = {
        id: performance.now(),
        term: name,
        records: recs
      }
      props.FilterResult(results)
      setIndex()
      setSelection({})
    }
  }

  let items = [];

  items.push(<div key={items.length}>
    [&nbsp;
    <a href="#" className="link-underline-primary me-2" onClick={SelectFilterItem}><b>Filter</b></a>
    <a href="#" className="link-underline-secondary me-2" onClick={SelectClearItem}>clear</a>
    <a href="#" className="link-underline-secondary" onClick={SelectInvertItem}>invert</a>
    &nbsp;]
  </div>)

  if (!getIndex) {
    let key = {}
    props.Results.forEach((item) => {
      let rec = props.Index.records[item]
      let hh = rec.Time.substring(0, 2)
      let mm = rec.Time.substring(3, 5)

      let texts = []

      texts.push("hh: " + hh)
      texts.push("mm: " + mm)

      texts.forEach(text => {
        if (!key[text]) {
          key[text] = [];
        }
        key[text].push(item)
      })
    });

    setIndex(key)
  }
  else {
    let keys = Object.keys(getIndex);
    keys.sort();
    keys.forEach((item) => {
      items.push(
        <span key={items.length} className={"badge me-2" + (getSelection[item] ? " text-bg-info" : " text-bg-secondary")} role="button"
          onClick={() => SelectItem(item)}
        >{item ? item : <span className="badge text-bg-warning">null</span>
          } <span style={{ fontWeight: 300 }}>({getIndex[item].length})</span>
        </span>)
    })
  }

  return (<>{items}</>);
}

export default FilterTime;
