import React, { useRef, useEffect } from 'react';
import lunr from 'lunr';

function SearchItem(props)
{
    const ref = useRef(null)
    const [getStatus, setStatus] = React.useState();
    const [getSearch, setSearch] = React.useState();
    const [getIdx, setIdx] = React.useState();
    const [getFound, setFound] = React.useState();
    const [getMode, setMode] = React.useState();

    const UpdateSearch = (e) => {
        if (e.code === 'Enter')
        {
            Search(ref.current.value)
        }
    }

    const Search = (term) =>
    {
        setSearch(term)
        setMode({
            mode: 'search'
        })
    }

    const BuildSearchIndex = () => {
        setMode({
            mode: 'start',
            pos: 0,
        })
        setStatus("Building Index...")
    }

    const FilterFound = () => {
        let results = {
            id: performance.now(),
            term: "lunr:" + getSearch,
            records: getFound
          }

        if (props.FilterResult)
        {
            props.FilterResult(results)
        }
    }

    useEffect(() => {
        if (!getMode)
        {
            return
        }

        if (getMode.mode === 'start')
        {
            let mode = {
                ...getMode
            }

            if (mode.pos == 0)
            {
                let builder = new lunr.Builder();
                builder.ref('id')
                builder.field('Message')
                mode.builder = builder
            }

            let n = 0;
            while (mode.pos < props.Results.records.length)
            {
                let id = props.Results.records[mode.pos]
                let rec = props.Index.records[id]
                //console.log('scan', rec)
                mode.builder.add({
                    id: id,
                    Message: rec.Message,
                })

                mode.pos++
                n++
                if (n >= 10000)
                {
                    break;
                }
            }
            
            if (mode.pos < props.Results.records.length)
            {
                setStatus("Record [" + mode.pos + " / " + props.Results.records.length + "]")
            }
            else
            {
                setStatus("Building Index")
                mode.mode = 'build'
            }
            setMode(mode)
        }

        if (getMode.mode === 'build')
        {
            let idx = getMode.builder.build()
            props.Results.idx = idx;
            setStatus()
            setMode()
        }

        if (getMode.mode === 'search')
        {
            console.log('searching:', getSearch)
            try
            {
                let matches = props.Results.idx.search(getSearch)

                let recs = []
                matches.forEach((item) => {
                    recs.push(item.ref)
                })

                setFound(recs)
                setStatus("Found: " + recs.length + " records.")
                setMode()
            }
            catch (e) {
                setFound([])
                setStatus(e.message)
                setMode()
            }
        }
    }, [getMode])

    let items = [];

    if (props.Results.idx)
    {
        items.push(<div>
            <input ref={ref} className="me-1" size={50} type='search' onKeyUp={UpdateSearch}/>
            <a href="#" className="link-underline-primary"
                onClick={ () => { Search(ref.current.value) }}
                >Search</a>
            </div>)
        if (getSearch || getStatus)
        {
            items.push(<div>
                <span className="badge text-bg-secondary">{getSearch}</span> {getStatus}
                </div>)
        }
        if (getFound && getFound.length > 0)
        {
            items.push(<div>
                [&nbsp;
                <a href="#" className="link-underline-primary"
                    onClick={FilterFound}
                    ><b>Filter</b></a>
                &nbsp;]
            </div>)
        }
    }
    else if (!getMode)
    {
        items.push(<div>
            <button className="btn btn-light"
                onClick={BuildSearchIndex}
            >Build</button>
        </div>)
    }
    else
    {
        items.push(<div>
            {getStatus}
        </div>)
    }

    return (
        <div className="mt-3">
            {items}
        </div>
    )

}

export default SearchItem;