import React from 'react';
import Icon from './Icon.js';
import './App.css';

function PageBar(props) {
  const [getPage, setPage] = React.useState(0);

  React.useEffect(() => {
    setPage(props.Page)
  }, [props.Page])

  const NextPage = (e) => {
    let n = Math.min(getPage + props.PageSize, props.Results.records.length - 1)
    let pg = Math.trunc(n / props.PageSize) * props.PageSize
    setPage(pg);

    if (props.OnPage) {
      props.OnPage(pg)
    }
  }

  const PrevPage = (e) => {
    let pg = Math.max(getPage - props.PageSize, 0)
    setPage(pg);

    if (props.OnPage) {
      props.OnPage(pg)
    }
  }

  const GotoPage = (n) => {
    let pg = Math.trunc(n / props.PageSize) * props.PageSize
    setPage(pg);

    if (props.OnPage) {
      props.OnPage(pg)
    }
  }

  let endPage = Math.min(getPage + props.PageSize, props.Results.records.length)

  let modals = []
  for (let n = 0; n < props.Results.records.length; n += props.PageSize) {
    let isCurPage = n >= getPage && n <= getPage

    let isFav = false;
    if (props.Favorite)
    {
      for (let j = n; j < Math.min(n + props.PageSize, props.Results.records.length); j++)
      {
        let id = props.Results.records[j]
        if (props.Favorite[id])
        {
          isFav = true
          break;
        }
      }
    }

    modals.push(<span className={isCurPage ? "badge btn text-bg-primary me-1 " : "badge btn text-bg-info me-1"}
      data-bs-dismiss="modal"
      onClick={() => { GotoPage(n+1) }}
      >{n + 1} {isFav && <Icon Kind='HeartFill'/>}</span>)
  }

  let items = []

  items.push(
    <div key={items.length} className="modal fade" id="pageModal" tabIndex={-1} aria-hidden={true}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-body">
            {modals}
          </div>
        </div>
      </div>
    </div>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" onClick={() => { GotoPage(0) }}>|&lt;</button>
  </span>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" onClick={PrevPage}>&lt;</button>
  </span>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" data-bs-toggle="modal" data-bs-target="#pageModal">{getPage + 1}-{endPage} / {props.Results.records.length}</button>
  </span>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" onClick={NextPage}>&gt;</button>
  </span>)

  items.push(<span className="mx-1" key={items.length}>
    <button className="btn btn-light" onClick={() => { GotoPage(props.Results.records.length - 1) }}>&gt;|</button>
  </span>)

  return (
    <span className="App">
      {items}
    </span>
  );
}

export default PageBar;
