import React from "react";

function FilterDistinct(props) {
  const [getStatus, setStatus] = React.useState()
  const [getIndex, setIndex] = React.useState()

  const pageSize = 1000

  const BuildIndex = (e, state, index) => {
    console.log('[Build]', 'State:', state)

    if (!state) {
      setStatus("Building...")

      setTimeout(() => {
        BuildIndex(null, 1, {});
      }, 500)

      return
    }

    // Add Records
    if (state < props.Results.records.length) {
      setStatus("At " + state)

      let start = state
      while (state < Math.min(start + 40000, props.Results.records.length)) {
        let id = props.Results.records[state]
        let rec = props.Index.records[id]
        let tokens = rec.Message.split(/[\s;]/)

        let cur = index
        tokens.forEach(term => {
          if (!cur[term]) {
            cur[term] = {}
          }
          cur = cur[term]
        })

        state++
      }

      setTimeout(() => {
        BuildIndex(null, state + 1, index);
      }, 500)

      return
    }

    // Compact Index

    console.log('[Index]', 'Before:', index)

    let root = []
    root.push(['$', index])

    while (root.length > 0) {
      let [key, obj] = root.pop();

      Object.keys(obj).forEach(item => {
        let subkeys = Object.keys(obj[item])
        if (subkeys.length === 1) {
          // Got a [1,1] pattern
          let subkey = subkeys[0]
          let subobj = obj[item][subkey]

          let comb = item + ' ' + subkey
          delete obj[item]
          obj[comb] = subobj

          root.push([comb, obj])
        }
        else {
          // Check on [1,*,1] pattern
          let t3 = {}
          subkeys.forEach(item2 => {
            let o2 = obj[item][item2]
            Object.keys(o2).forEach(k3 => {
              t3[k3] = 1
            })
          })

          let sk3 = Object.keys(t3)
          if (sk3.length === 0 && subkeys[0])
          {
            let comb = item + ' **[' + subkeys[0] + ']**'
            obj[comb] = {}

            subkeys.forEach(key => {
              delete obj[key]
            })

            delete obj[item]

            //root.push([comb, obj])
          }
          else if (sk3.length === 1)
          {
            let comb = item + ' **[' + subkeys[0] + ']** ' + sk3[0]
            obj[comb] = obj[item][subkeys[0]][sk3[0]]

            subkeys.forEach(key => {
              // Need to MERGE into obj[comb] before delete
              delete obj[key]
            })

            delete obj[item]

            root.push([comb, obj])
          }
        }
      })
    }

    console.log('[Index]', 'After:', index)

    setStatus('Done!')
    setIndex(index)
  }

  let items = [];

  items.push(<div>Status: {getStatus}</div>)
  items.push(<div className="btn btn-info" onClick={BuildIndex}>Analyze</div>)

  if (getIndex) {
    let terms = []
    Object.keys(getIndex).forEach(item => {
      terms.push(<div className="border">
        {item}
      </div>)
    })
    items.push(<div>
      {terms}
    </div>)
  }

  return <>{items}</>
}

export default FilterDistinct;
