import React from 'react';
import './App.css';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, ArcElement, Title, Tooltip, Legend } from "chart.js";
import { Doughnut, Bar } from 'react-chartjs-2';

function ChartSummary(props) {
  const [getHistogram, setHistogram] = React.useState()
  const [getFormat, setFormat] = React.useState("hh")

  let items = []

  ChartJS.register(CategoryScale, LinearScale, BarElement, ArcElement, Title, Tooltip, Legend);

  const FormatSnapTime = (fmt, dt) => {
    let d = new Date()
    let key = "-"

    if (fmt === "dd") {
      let period = 24 * 60 * 60 * 1000
      dt = period * Math.trunc(dt / period)
      d.setTime(dt)

      key =
        d.getUTCFullYear().toString() + "-" +
        (100 + d.getUTCMonth()).toString().substring(1, 3) + "-" +
        (100 + d.getUTCDate()).toString().substring(1, 3)
    }
    else if (fmt === "hh") {
      let period = 60 * 60 * 1000
      dt = period * Math.trunc(dt / period)
      d.setTime(dt)

      key =
        (100 + d.getUTCMonth()).toString().substring(1, 3) + "-" +
        (100 + d.getUTCDate()).toString().substring(1, 3) + " " +
        (100 + d.getUTCHours()).toString().substring(1, 3) + ":" +
        (100 + d.getUTCMinutes()).toString().substring(1, 3)
    }
    else if (fmt === "mm") {
      let period = 60 * 1000
      dt = period * Math.trunc(dt / period)
      d.setTime(dt)

      key =
        (100 + d.getUTCHours()).toString().substring(1, 3) + ":" +
        (100 + d.getUTCMinutes()).toString().substring(1, 3) + ":" +
        (100 + d.getUTCSeconds()).toString().substring(1, 3)
    }
    else if (fmt === "ss") {
      let period = 1000
      dt = period * Math.trunc(dt / period)
      d.setTime(dt)

      key =
        (100 + d.getUTCHours()).toString().substring(1, 3) + ":" +
        (100 + d.getUTCMinutes()).toString().substring(1, 3) + ":" +
        (100 + d.getUTCSeconds()).toString().substring(1, 3)
    }

    return key
  }

  const BuildHistogram = () => {
    let histogram = {}

    let minDT
    let maxDT
    props.Results.forEach((id) => {
      let rec = props.Index.records[id];
      let dt = Date.parse(rec.Date + " " + rec.Time + "Z")
      if (!minDT || dt < minDT) {
        minDT = dt
      }
      if (!maxDT || dt > maxDT) {
        maxDT = dt
      }
    })

    let fmt = getFormat

    let period = 60 * 1000
    if (fmt === "dd")
    {
      period = 24 * 60 * 60 * 1000
    }
    else if (fmt === "hh")
    {
      period = 60 * 60 * 1000
    }
    else if (fmt === "mm")
    {
      period = 60 * 1000
    }
    else if (fmt === "ss")
    {
      period = 1000
    }
  
    let minDTsnap = period * Math.trunc(minDT / period)
    let maxDTsnap = period * Math.trunc(maxDT / period)
    console.log("[ChartSummary]", "Min/Max:", minDT, maxDT, minDTsnap, maxDTsnap)

    let d = new Date()
    for (let dt = minDTsnap; dt < maxDTsnap + period; dt += period) {
      let key = FormatSnapTime(fmt, dt)
      histogram[key] = {
        "1": 0,
        "2": 0,
        "3": 0,
        "4": 0,
      }
    }

    props.Results.forEach((id) => {
      let rec = props.Index.records[id];

      let dt = Date.parse(rec.Date + " " + rec.Time + "Z")
      let key = FormatSnapTime(fmt, dt)

      let k = rec.Level;
      if (histogram[key])
      {
        histogram[key][k]++;
      }
      else
      {
        console.log("[ChartSummary]", "missing key:", key)
      }
    })

    console.log("[Histogram]", histogram)

    return histogram;
  }

  let histogram = getHistogram;
  if (!histogram) {
    histogram = BuildHistogram();
    setHistogram(histogram);
  }

  let label = [];
  let l1 = [];
  let l2 = [];
  let l3 = [];
  let l4 = [];

  Object.keys(histogram).forEach((dt) => {
    label.push(dt)

    let v1 = histogram[dt]['1']
    l1.push(v1 ? v1 : 0);

    let v2 = histogram[dt]['2']
    l2.push(v2 ? v2 : 0);

    let v3 = histogram[dt]['3']
    l3.push(v3 ? v3 : 0);

    let v4 = histogram[dt]['4']
    l4.push(v4 ? v4 : 0);
  })

  let options = {
    scales: {
      x: {
        stacked: true
      },
      y: {
        stacked: true
      }
    }
  }

  let bardata = {
    labels: label,
    datasets: [
      {
        label: 'Info',
        data: l4,
        backgroundColor: 'rgba(176, 191, 26, 0.5)'
      },
      {
        label: 'Warning',
        data: l3,
        backgroundColor: 'rgba(124, 185, 232, 0.5)'
      },
      {
        label: 'Error',
        data: l2,
        backgroundColor: 'rgba(255, 126, 0, 0.5)'
      },
      {
        label: 'Critical',
        data: l1,
        backgroundColor: 'rgba(211, 33, 45, 0.5)'
      }
    ]
  }

  items.push(<Bar className="chartblock-1" key={items.length} options={options} data={bardata} />)

  let scales = [
    [ "day", "dd" ],
    [ "hour", "hh" ],
    [ "minute", "mm" ],
    [ "second", "ss" ],
  ]

  let scaleitems = []
  scales.forEach(item => {
    const [name, fmt] = item
    scaleitems.push(
      <span className="mx-1">
      { getFormat === fmt ?
        <b>{name}</b>
        :
        <a href="#" onClick={() => { setFormat(fmt); setHistogram(); }}>{name}</a>
      }
    </span>
    )
  })

  items.push(<div>
    <span className="mx-2">Scale:</span>
    {scaleitems}
  </div>)

  return (
    <>
      {items}
    </>
  );
}

export default ChartSummary;
