import React, { useLayoutEffect, useRef, useState } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.css'
import Icon from './Icon.js';

function Record(props) {
  const ref = useRef(null)
  const [getWidth, setWidth] = useState(80)

  useLayoutEffect(() => {
    const updateSize = () => {
      setWidth(Math.trunc(ref.current.offsetWidth / 9));
    }
    window.addEventListener('resize', updateSize);
    updateSize()
    return () => {
      window.removeEventListener('resize', updateSize)
    }
  }, [])

  const ToggleFavorite = (e) =>
  {
    if (props.onFavorite)
    {
      props.onFavorite(e)
    }
  }

  const FilterActivity = (type, id) =>
  {
    if (props.onFilterActivity)
    {
      props.onFilterActivity(type, id)
    }
  }

  let rec = props.Record

  let small = {
    fontSize: '10px'
  }

  let fullfilefunc = rec.File + "|" + rec.Function + "@" + rec.Line
  let filefunc = fullfilefunc
  if (fullfilefunc.length > 50)
  {
    filefunc = rec.File + "@" + rec.Line
  }

  let items = [];

  let color = (rec.Level==='1' ? 'text-bg-critical' :
    rec.Level==='2' ? 'text-bg-danger' : 
    'text-bg-info')
  items.push(<span key={items.length} className={"badge rounded-pill mx-1 "+color}>{rec.Level}</span>)
  items.push(<span key={items.length}className="badge text-bg-info mx-1">{rec.Date} {rec.Time}</span>)
  items.push(<span key={items.length} className="badge text-bg-info mx-1" style={small}>{rec.Stream} | {rec.ErrorType}</span>)
  items.push(<span key={items.length} className="badge text-bg-info mx-1" style={small}>Pid:{rec.Pid} Tid:{rec.Tid} #{rec.Instance}</span>)
  items.push(<span key={items.length} className="badge text-bg-info mx-1" style={small} title={fullfilefunc}>{filefunc}</span>)
  if (rec.MDRESULT || rec.ErrorCodeMsg)
  {
    items.push(<span key={items.length} className="badge text-bg-warning mx-1" style={small}>{rec.MDRESULT} | {rec.ErrorCode}:{rec.ErrorCodeMsg}</span>)
  }
  else
  {
    items.push(<span key={items.length} className="badge text-bg-info mx-1" style={small}>{rec.MDRESULT} | {rec.ErrorCode}:{rec.ErrorCodeMsg}</span>)
  }

  items.push(
    <span key={items.length} className="mx-1" onClick={() => { ToggleFavorite(rec.id); }} >
      <Icon Kind={props.Favorite[rec.id] ? 'HeartFill' : 'Heart'} />
    </span>
  )

  items.push(
    <span key={items.length} className="mx-1" onClick={() => { FilterActivity('ActivityId', rec.id); }} >
      <Icon Kind='Magnifier' />
    </span>
  )

  let text = []
  let textlen = getWidth
  let p = 0;
  while (p < rec.Message.length)
  {
    let msgseg = rec.Message.slice(p, p+textlen)
    text.push(<p key={text.length} className="word-nowrap mb-0">{msgseg}</p>)
    p+= textlen;
  }

    // Object.keys(rec).forEach( (item) => {
  //   items.push(<span key={items.length} className="badge text-bg-primary mx-1">{item}:{rec[item]}</span>)
  // }
  // )

  return (
    <div className="record btn-light text-start mb-1">
      <div>
        {items}
      </div>
      <div ref={ref} style={{fontFamily: "consolas"}} >
        {text}
      </div>
    </div>
  );
}

export default Record;
